import React from 'react';
import {setview, setcategories, setprojects, setchosenproject, setfetchsuccess} from './store/actions';
import {connect} from 'react-redux';
import './fontpack.css';
import './transform.css';
import { CSSTransition } from 'react-transition-group';
import 'react-rangeslider/lib/index.css';
import styled from 'styled-components';
import useMediaQuery from '@material-ui/core/useMediaQuery';


const sliderThumbStyles = (props) => (`
        width: 4px;
        height: 20px;
        background: ${props.color}
        opacity: ${props.opacity};
        -webkit-transition: .3s;
        transition: all .3s;
`)
var onlyone = true;


const Styles = styled.div`
color: #FF0000;
display: flex;
align-items: center;
.slider{
    flex: 6;
    -webkit-appearance: none;
    width: 100%;
    height: 1.5px
    cursor: pointer;
    border-radius: 1.5px;
    background: #efefef;
    outline: none;
    &::-webkit-slider-thumb{
        -webkit-appearance: none;
        appearance: none;
        ${props => sliderThumbStyles(props)}
    }
    &::-moz-range-thumb {
        -webkit-appearance: none;
        appearance: none;
        ${props => sliderThumbStyles(props)}
    }
}
`;


function MenuBar(props) {
    // eslint-disable-next-line
    const matches = useMediaQuery('(min-width:600px)');
  
    const [updated, setUpdated] = React.useState(false);
    const fetchAndSetData = () => { 
        var data
        Promise.all([
          fetch('http://localhost:1337/categories').then(value => value.json()),
          fetch('http://localhost:1337/projects').then(value => value.json())
        ]).then(([catgs, projcs]) => {
          console.log("categories are loaded")
          console.log(catgs)
          console.log("projects are loaded")
          console.log(projcs)
      
      
         // data = getData(categories, projects)
         catgs.forEach((element, index) => {
          element.id = [element.id, index];
        })
        projcs.forEach((element,index) => {
          element.catg = catgs.find(cat => cat.id[0]===element.catg).id[1]
          element.img = element.img.url
          if(element.scheme!==null)
            element.scheme = element.scheme.url
        });
        projcs.sort(function(a,b){
            return a.catg-b.catg
        })
        projcs.forEach((element,index) => {
            element.id = index
          });
        
        catgs.forEach((element, index) => {
          element.id = index;
        })
      
          props.setcategories(catgs)
          props.setprojects(projcs)
      
        })
        .catch((err) => {
            console.log(err);
      
        });
      
        return data
      }
      
        

    const showvalues = () => {
        console.log(props.categories)
        console.log(props.projects)
    };


   React.useEffect(()=>{
       if((props.fetchsuccess&&onlyone===true)){
           console.log("fetch success")
            handleProjectClick(1)
            updateProjectsAndCategories(1)
            onlyone = false 
       }
       if(!updated){
        fetchAndSetData()
       }
       setUpdated(true)
   });
    const [slidervalue, setSlidervalue] = React.useState(0);
    // const loadProject = (id, name) => {alert(id + name); }

    const handleCategoryClick = (val) => (event) => {
        console.log("handle Category Click")
        var cat = props.categories
        var tcat = cat.find(p=>p.selected===true);
        tcat.selected = false;
        cat[val].selected=true;
        props.setcategories(cat)
        if(tcat.id!==val){
            const va = props.projects.filter(p=>p.catg===val)[0].id
            var projcs = props.projects;
            projcs.find(p=>p.selected===true).selected=false;
            projcs[va].selected=true;
            props.setchosenproject(projcs[va])
            props.setprojects(projcs)
            const fdprojects = projcs.filter(e=>e.catg===props.categories.find(c=>c.selected===true).id)
            setSlidervalue(fdprojects[0].id)
        }

    }

    const handleProjectClick = (val) => (event) => {
        console.log("handle Project Click")

        var projcs = props.projects;
        console.log(props.projects)
        projcs.find(p=>p.selected===true).selected=false;
        projcs[val].selected=true; 
        props.setchosenproject(projcs[val])
        props.setprojects(projcs)
        setSlidervalue(val)
    }



    const updateProjectsAndCategories=(project_id) => {
        console.log("update Projects And Categories")

        var val = project_id
        var projcs = props.projects;
        projcs.find(p=>p.selected===true).selected=false;
        console.log(val+"project to be updated")
        projcs[val].selected=true; 
        props.setchosenproject(projcs[val])
        var chosenCategory = projcs[val].catg;
        props.categories.find(c=>c.selected===true).selected=false;
        var ctgs = props.categories;
        ctgs[chosenCategory].selected=true;
        props.setcategories(ctgs);
        props.setprojects(projcs)
        setSlidervalue(val)

    }

    const handleOnChange = (e) => {
        updateProjectsAndCategories(e.target.value)
    }

    const handleClick = (val) => (event) => {
        if(!props.fetchsuccess)
            props.setfetchsuccess(true)

        console.log("handle Click")

        if(val==='p'){
            props.setview("projects")
        }else if(val==='a'){
            props.setview("about")
        }else{
            props.setview("contact")
        }
    }
    const handleArrowClick = (v) => (e) =>{
        if(v>=0||v<props.projects.length)
        updateProjectsAndCategories(v)
    }
    return (
        <div>
        <div  className='transform disable-select' onClick={()=>props.setview("home")} style={{paddingLeft: "calc(15% - 50px)", paddingTop:"12px", cursor: "pointer", paddingBottom: "8px",  fontSize:"28px", fontFamily: "Quicksand"}}>
          KRZYSZTOF PRZYBYŁO
        </div>
        <div style={{width: "100%", display: "inline", backgroundColor: "red", textAlign:"center", fontFamily: "Quicksand", fontSize: "21px"}}>
            <hr style ={{border: "0.5px solid lightgrey"}}></hr>
                <div className='transform disable-select' onClick={handleClick('p')} value="projects"style={{float: "left", width: "30%", cursor: "pointer", color: props.view ==="projects" ? "black":"lightgrey"}}>PROJECTS</div>
                <div  className='transform disable-select' onClick={handleClick('a')} value="about" style={{float: "left", width: "40%",cursor: "pointer", color: props.view ==="about"  ? "black":"lightgrey"}}>ABOUT</div>
                <div  className='transform disable-select' onClick={handleClick('c')} value="contact" style={{float: "left", width: "30%",cursor: "pointer", color: props.view ==="contact"  ? "black":"lightgrey"}}>CONTACT</div>
                <br></br>
                <CSSTransition
                    in={(props.view==="projects")}
                    timeout={{
                        appear: 200,
                        enter: 400,
                        exit: 0,
                       }}
                    classNames="alert"
                    unmountOnExit
                    style={{float: "left", textAlign: "left", paddingLeft: "calc(16% - 70px)", width: "calc(84% + 35px)", fontSize: "calc(14px + 0.35rem)"}}>
                    <div style={{display: "inline"}}>
                        {props.categories.map(c=><div onClick={handleCategoryClick(c.id)} style={{cursor: "pointer", color: c.selected===true? "black":"lightgrey", paddingLeft: "15px", float: "left"}}>{c.name}</div>)}
                    </div>
                </CSSTransition>



                <CSSTransition
                    in={props.view==="projects"}
                    timeout={{
                        appear: 200,
                        enter: 400,
                        exit: 0,
                       }}
                     classNames="alert"
                    unmountOnExit
                    style={{float: "left",textAlign: "left", paddingLeft: "calc(16% - 63px)", width: "calc(84% + 31px)", fontSize: "calc(14px + 0.35rem)"}}>
                    <div style={{display: "inline"}}>
                      {props.projects.filter(e=>e.catg===props.categories.find(c=>c.selected===true).id).map(p=><div className='transform disable-select' onClick={handleProjectClick(p.id)} value={p.id} style={{cursor: "pointer", color: p.selected===true? "black":"lightgrey", paddingLeft: "15px", float:"left"}}>{p.name}</div>)}
                    </div>
                </CSSTransition>

                <br></br>

                <CSSTransition
                    in={props.view==="projects"}
                    timeout={{
                        appear: 200,
                        enter: 400,
                        exit: 0,
                       }}
                    classNames="alert"
                    unmountOnExit
                    style = {{width: "calc(250px + 18vw)", paddingTop: "calc(50px - 0.5vw)", paddingBottom: "calc(30px - 2vw)", marginLeft: "auto", marginRight: "auto"}}>
                        <div>
                        <div style={{display: "inline"}}> 

                            <Styles opacity={0.5+slidervalue/20} color={"lightgrey"} style={{width: "100%"}}>
                                <div style = {{fontFamily:"DM Sans", color: "lightgrey", fontSize: "28px", cursor: "pointer"}} className="disable-select" onClick={handleArrowClick(slidervalue<=0? slidervalue:parseInt(slidervalue)-1)} >
                                    &lt;
                                </div>
                                <input ref={input => input && input.focus()} type='range' value = {slidervalue} min={0} max={props.projects.length-1} className='slider' onChange={handleOnChange}></input>
                                <div style = {{fontFamily:"DM Sans", color: "lightgrey", fontSize: "28px", cursor: "pointer"}} className="disable-select" onClick={handleArrowClick(slidervalue>=props.projects.length-1? slidervalue:parseInt(slidervalue)+1)}>
                                    &gt;
                                </div>
                            </Styles>
                          
                        </div>
                        </div>
                        
                </CSSTransition>

        </div>
        </div>
    );
}

const mapStateToProps = (state) => ({
    view: state.view,
    projects: state.projects,
    categories: state.categories,
    chosenproject: state.chosenproject,
    fetchsuccess: state.fetchsuccess
  });
  
export default connect(
    mapStateToProps,
    {setview,setcategories, setprojects, setchosenproject, setfetchsuccess}
  )(MenuBar);
